import _ from 'lodash';

import { FETCH_EXPERTS } from '../actions'

export default function (state = {}, action) {
    switch (action.type) {
        case FETCH_EXPERTS:
            return _.mapKeys(action.payload.data, 'id')

        default:
            return state;
    }
}


